import React, { Component } from 'react';
import '../clubview.css';
import { Parser } from 'html-to-react';
import Error404 from './Error404';

let DEV_URL = 'https://clubsource.herokuapp.com';

if (process.env.NODE_ENV === 'development') {
	DEV_URL = 'http://localhost:3000';
}

console.log(process.env.NODE_ENV);

class ViewClub extends Component {
	constructor(props){
		super(props);
		this.state = {
			club: {},
			exists: true
		}
	}
	
	async componentDidMount() {
		var parser = new Parser();
		var username = this.props.match.params.username;
		console.log({DEV_URL});
		const res = await fetch(`${DEV_URL}/searchData/${username}`);
		const club = await res.json();
		console.log(club);
		if (club === 'not found') {
			console.log('page not found');
			this.setState({exists: false});
			this.forceUpdate(); //probably unnecessary
			return;
		}
		club.description = parser.parse(club.description);
		this.setState({club: club});
	}
	
	render() {
		const ClubPage = () => (
			<div className='container-fluid'>
				<div className='row'>
					<div className='col sidead'>
						Left Ad
					</div>
					
					<div className='col'>
						
						<div>
							<h1 className='title'>{this.state.club.clubname}</h1>
							<p className='username'>{this.state.club.username}</p>
							<div className='info'>
								<p className='gray'>Tags: {this.state.club.tags}</p>
								<p className='gray'>Officers: {this.state.club.officers}</p>
								<p className='gray'>Meeting dates: {this.state.club.meetingdates}</p>
								<hr />
								<div className='quillcontent'>{this.state.club.description}</div>
							</div>
						</div>
						
					</div>
					
					<div className='col sidead'>
						Right Ad
					</div>
				</div>
			</div>
			
		);
		
		var exists = this.state.exists;
		console.log(exists);
		
		return(
			<div>
				{exists ? (<ClubPage />) : (<Error404 />)};
			</div>
		);
	}
}

export default ViewClub;