import React, { Component } from 'react';

let DEV_URL = 'https://clubsource.herokuapp.com';

if (process.env.NODE_ENV === 'development') {
	DEV_URL = 'http://localhost:3000';
}

class Navbar extends Component {
	constructor(props){
		super(props);
		this.state = {
			
		};
	}
	
	render() {
		return (
			<div className='navbar'>
				<div className='navcont navleft'>
					<a className='mainlogonav' href={DEV_URL}> clubsource</a>
				</div>
				<div className='navcont navright'>
					<a className='navlink' href={DEV_URL + '/dashboard'}>Dashboard</a>
					<a className='navlink' href={DEV_URL + '/clubs/search'}> Browse Clubs</a>
					<a className='navlink' href={DEV_URL + '/accounts/logout'}>Log Out</a>
				</div>
			</div>
		)
	}
}

export default Navbar;